module.exports = "<table style=\"display: table; margin-bottom: 1.25rem\"><thead><tr style=\"line-height: 1.5\"><th style=\"width: 10%\">Prop Name</th><th style=\"width: 25%\">Type</th><th style=\"width: 10%\">Is Required</th><th style=\"width: 10%\">Default Value</th><th style=\"width: 45%\">Description</th></tr></thead><tbody><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">action</td><td style=\"padding-bottom: 1.8rem\">element</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">null</td><td style=\"padding-bottom: 1.8rem\">An action element to be added to the action section of the alert.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">children</td><td style=\"padding-bottom: 1.8rem\">node</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">''</td><td style=\"padding-bottom: 1.8rem\">Child Nodes providing the message content for the alert. Can contain text and HTML.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">customIcon</td><td style=\"padding-bottom: 1.8rem\">element</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">null</td><td style=\"padding-bottom: 1.8rem\">The icon to be used for an alert of type custom. This will not be used for any other alert types.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">customColorClass</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">'custom-default-color'</td><td style=\"padding-bottom: 1.8rem\">Sets an author-defined class, to control the status bar color to be used for an alert of type custom.<img src=\"https://badgen.net/badge//IMPORTANT/CSS?icon=github\" alt=\"IMPORTANT\">\nAdding <code style=\"white-space: pre-wrap\">var(--my-app...</code> CSS variables is required for proper re-themeability when creating custom color styles <em>(see included examples)</em>.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">onDismiss</td><td style=\"padding-bottom: 1.8rem\">func</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">null</td><td style=\"padding-bottom: 1.8rem\">Callback function triggered when Dismiss button is clicked. The presence of this prop will cause the Dismiss button to be included on the alert.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">title</td><td style=\"padding-bottom: 1.8rem\">string</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">''</td><td style=\"padding-bottom: 1.8rem\">The title for the alert which will be bolded.</td></tr><tr style=\"vertical-align: top\"><td style=\"font-weight: bold; padding-bottom: 1.8rem\">type</td><td style=\"padding-bottom: 1.8rem\">enum</td><td style=\"padding-bottom: 1.8rem\">optional</td><td style=\"font-weight: bold; padding-bottom: 1.8rem\">'alert'</td><td style=\"padding-bottom: 1.8rem\">The type of alert to be rendered. One of <code style=\"white-space: pre-wrap\">alert</code>, <code>error</code>, <code>warning</code>, <code>advisory</code>,\n<code>info</code>, <code>success</code>, <code>custom</code>.</td></tr></tbody></table>"